<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>
        <v-flex sm12>
            <h3>Usuário</h3>
        </v-flex>

        <v-flex sm12>
            <v-card color="blue-grey darken-2" class="white--text">
                <v-card-title primary-title>
                    <div>
                        <div class="headline">{{user.data.name}}</div>
                        <span>{{user.data.name.email}}</span>
                    </div>
                </v-card-title>
                <!-- <v-card-actions>
                    <v-btn flat dark>Listen now</v-btn>
                </v-card-actions> -->
            </v-card>
        </v-flex>

        <v-flex sm12>
            <h3>Enviar arquivo</h3>
        </v-flex>
        <v-flex xs12 sm12 md12>
            <v-card>
                <v-flex xs12 sm12 md12>
                    <v-form ref='uploadForm' id='uploadForm' v-bind:action='mUrl' method='post' encType="multipart/form-data">
                        <v-text-field v-model="title" label="titulo" required></v-text-field>
                        <input class="v-btn" type="file" name="sampleFile" />
                        <input class="v-btn primary" type='submit' value='Upload!' />
                </v-form>
                </v-flex>
            </v-card>
        </v-flex>

        <v-flex sm12>
            <h3>Arquivo enviados</h3>
        </v-flex>

        <v-flex xs12 sm12 md12>
            <v-card>
                <v-list subheader>
                    <v-list-tile v-for="item in filers" :key="item._id">
                        <v-list-tile-content>
                            <v-list-tile-title v-html="item.title"></v-list-tile-title>
                            <v-list-tile-sub-title>{{item.createDate}}</v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</v-container>
</template>

<script>
import services from "../services/userService";
export default {
    data() {
        return {
            valid: false,
            user: null,
            filers: null,
            title: '',
            rota: 'http://191.252.200.167:3000/file/send',
            url: '',
        }
    },
    methods: {
        listarArquivos(id) {

            services.getFiles(id).then(r => {
                if (r.status == 200) {
                    var arquivos = [];
                    var arquivo = {};
                    for (let index = 0; index < r.data.length; index++) {
                        arquivo = r.data[index];
                        arquivo.createDate = this.formatDate(r.data[index].createDate);
                        arquivos.push(arquivo);
                    }
                    this.filers = arquivos;
                }
            })
        },
        formatDate(date) {
            var mDate = new Date().toISOString().
            replace(/T/, ' '). // replace T with a space
            replace(/\..+/, '');
            if (!mDate) return null
            const [year, month, day] = mDate.split('-')
            const [mDay, horas] = day.split(' ')
            return `${mDay}/${month}/${year} - ${horas}`
        },
    },
    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.listarArquivos(this.user.data.id);
    },
    computed: {
        mUrl() {
            if (this.user != null) {
                return this.url = this.rota + '/' + this.title + '/' + this.user.data.id;
            }
        }
    }
}
</script>
